import React, { Component } from "react";
import logo from "../../images/logo-brand.png";
import "./style.css";
import Form from "./Register.form";
import { Link } from "react-router-dom";
class Register extends Component {
  render() {
    return (
      <div class="main-wrapper">
        <div class="auth-wrapper d-flex no-block justify-content-center align-items-center bg-register">
          <div class="auth-box">
            <div>
              <div class="logo">
                <span class="db">
                  <Link to="/">
                    <img src={logo} className="register-logo" alt="logo" />
                  </Link>
                </span>{" "}
                <h5 className="font-medium m-b-20"> Đăng ký tài khoản </h5>{" "}
              </div>{" "}
              <div class="row">
                <div class="col-12">
                  <Form />
                </div>{" "}
              </div>{" "}
            </div>{" "}
          </div>{" "}
        </div>{" "}
      </div>
    );
  }
}

export default Register;
