import React from "react";
import { Link } from "react-router-dom";
const Post = ({ data }) => {
  return (
    <div className="post-preview row">
      <div className="col-sm-4">
        <img src={data.image} className="post-image" alt="post" />
      </div>
      <div className="col-sm-8">
        <Link
          className="post-link"
          to={{
            pathname: `/forum/${data.forum_id}`,
            state: { id: data.forum_id }
          }}
        >
          <h5 className="news-title">{data.title}</h5>
        </Link>
        <p className="post-meta">
          <Link
            to={{
              pathname: `/users/${data.author_id}`
            }}
          >
            {" "}
            {data.author}
          </Link>{" "}
          - {data.created_at}
        </p>
      </div>
    </div>
  );
};

export default Post;
