module.exports.storeViewData = function storeViewData(key, value) {
  window.localStorage.setItem(key, value);
};
module.exports.removeViewData = function removeViewData(...key) {
  window.localStorage.removeItem(key);
};
module.exports.getViewData = function getViewData(key) {
  return window.localStorage.getItem(key);
};
module.exports.initializeStore = function initializeStore() {
  window.localStorage.clear();
};
