import React, { Component } from "react";
import Loading from "../../Functions/Loading";
import NewsItem from "./News.item.all";
import Axios from "axios";
class News extends Component {
  state = {
    total: null,
    data: []
  };
  componentDidMount() {
    Axios.get("http://localhost:8888/news")
      .then(res => {
        this.setState({ total: res.data.counts, data: res.data.data });
        console.log(this.state.data);
      })
      .catch(err => console.log(err));
  }
  render() {
    const { data } = this.state;
    return (
      <div className="container">
        <div className="title-section">
          <h1 class="cat-title"> Tin tức eSports </h1>
          <h5 className="cat-subtitle">
            Tin tức về thế giới eSports được cập nhật 24/24
          </h5>
        </div>
        <div className="row">
          <form className="col-sm-5" onSubmit={this.handleSubmit}>
            <input type="text" className="form-control" placeholder="Find..." />
            <i className="search-icon fas fa-search" />
          </form>
        </div>
        {data.length > 0 ? (
          data.map(item => {
            return <NewsItem data={item} key={item.news_id} />;
          })
        ) : (
          <Loading />
        )}
        <nav
          style={{ float: "left", marginTop: 30 }}
          aria-label="Page navigation example"
        >
          <ul class="pagination justify-content-center">
            <li class="page-item disabled">
              <a class="page-link" href="." tabindex="-1">
                {'<'}
              </a>
            </li>
            <li class="page-item">
              <a class="page-link" href=".">
                1
              </a>
            </li>
            <li class="page-item">
              <a class="page-link" href=".">
                2
              </a>
            </li>
            <li class="page-item">
              <a class="page-link" href=".">
                3
              </a>
            </li>
            <li class="page-item">
              <a class="page-link" href=".">
                {'>'}
              </a>
            </li>
          </ul>
        </nav>
      </div>
    );
  }
}

export default News;
