import React, { Component } from "react";
import Axios from "axios";
import { getViewData } from "../../Functions/LocalStorage";
import Pending from "../../Functions/Pending";
class uploadHighlight extends Component {
  state = {
    genre: null,
    video: null,
    title: null,
    description: null,
    uploadStatus: "no",
    data: []
  };
  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
    console.log(this.state);
  };
  handleVideoChange = e => {
    this.setState({ [e.target.name]: e.target.files[0] });
  };
  handleSubmit = e => {
    e.preventDefault();
    this.setState({ uploadStatus: "pending" });
    console.log(this.state);
    // Loading while waiting

    const fd = new FormData();
    fd.append("video", this.state.video);
    fd.append("genre", this.state.genre);
    fd.append("token", getViewData("auth-token"));
    fd.append("title", this.state.title);
    fd.append("description", this.state.description);
    Axios.post("http://localhost:8888/highlights/create", fd)
      .then(res => {
        this.setState({ data: res.data, uploadStatus: "yes" });
        console.log(res.data);
      })
      .catch(err => console.log(err));
  };
  render() {
    const { uploadStatus} = this.state;
    return (
      <form onSubmit={this.handleSubmit}>
        <div class="form-group">
          <label for="exampleFormControlSelect1">Thể loại game</label>
          <select
            name="genre"
            class="form-control"
            id="exampleFormControlSelect1"
            onChange={this.handleChange}
          >
            <option value="">-- Thể loại game --</option>
            <option value="Liên Minh Huyền Thoại">Liên Minh Huyền Thoại</option>
            <option value="PUBG">PUBG</option>
            <option value="Liên Quân Mobile">Liên Quân Mobile</option>
            <option value="Dota 2">Dota 2</option>
            <option value="Khác">Khác</option>
          </select>
        </div>
        <div class="form-group">
          <label for="exampleInputEmail1">Tiêu đề</label>
          <input
            type="text"
            class="form-control"
            onChange={this.handleChange}
            name="title"
            id="hl-tieude"
            placeholder="Tiêu đề highlight"
          />
        </div>
        <div class="form-group">
          <label for="exampleInputPassword1">Mô tả thêm về video</label>
          <textarea
            type="text"
            name="description"
            onChange={this.handleChange}
            class="form-control"
            placeholder="Mô tả về highlight của bạn..."
          />
        </div>
        <div class="form-group">
          <label for="exampleFormControlFile1">Video clip của bạn</label>
          <input
            name="video"
            type="file"
            onChange={this.handleVideoChange}
            class="form-control-file"
          />
        </div>
        <button type="submit" class="btn btn-primary">
          Gửi
        </button>
        {uploadStatus === "yes" ? (
          <div class="status-message alert alert-success" role="alert">
            <i class="fas fa-check" /> Đã xong ! Hệ thống sẽ đăng trong vòng 5 -
            10 phút sau khi kiểm duyệt{" "}
          </div>
        ) : (
          [uploadStatus === "pending" ? <Pending /> : ""]
        )}
      </form>
    );
  }
}

export default uploadHighlight;
