import React, { Component } from "react";
import HighlightItem from "./Highlight.item.all";
import Loading from "../../Functions/Loading";
import Axios from "axios";
import Form from "./uploadHighlight";
class Highlight extends Component {
  state = {
    total: null,
    data: []
  };
  componentDidMount() {
    Axios.get("http://localhost:8888/highlights")
      .then(response => {
        this.setState({
          total: response.data.counts,
          data: response.data.data
        });
        console.log(this.state.data);
      })
      .catch(err => console.log(err));
  }
  render() {
    const { data } = this.state;
    return (
      <div className="container">
        <div className="highlight-section">
          <div className="title-section">
            <h1 class="cat-title"> Thư viện highlight </h1>
            <h5 className="hl-cat-subtitle">
              "Lowlight" hay highlight ? Tất cả đều ở{" "}
              <u className="bold-title">thư viện highlight</u>
            </h5>
          </div>
          <div className="upload-btn">
            <button
              className="btn btn-danger upload"
              type="button"
              data-toggle="modal"
              data-target="#exampleModalLong"
            >
              {" "}
              <i class="fas fa-cloud-upload-alt" /> Tải highlight của bạn
            </button>
          </div>
          <div className="col-sm-12 row ">
            {data.length > 0 ? (
              data.map(item => {
                return <HighlightItem data={item} key={item._id} />;
              })
            ) : (
              <Loading />
            )}
          </div>
          <div
            class="modal fade"
            id="exampleModalLong"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLongTitle"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content">
                <div class="modal-header">
                  <h5 class="modal-title" id="exampleModalLongTitle">
                    Tải highlight cá nhân
                  </h5>
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">
                  <Form />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Highlight;
