import React, { Component } from "react";
import CKEditor from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { getViewData } from "../../Functions/LocalStorage";
import Pending from "../../Functions/Pending";
import Axios from "axios";
class uploadPost extends Component {
  state = {
    title: null,
    content: null,
    image: null,
    game: null,
    postStatus: "no"
  };
  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };
  handleVideoChange = e => {
    this.setState({ [e.target.name]: e.target.files[0] });
  };
  handleSubmit = e => {
    e.preventDefault();
    this.setState({ postStatus: "pending" });
    const fd = new FormData();
    fd.append("token", getViewData("auth-token"));
    fd.append("title", this.state.title);
    fd.append("content", this.state.content);
    fd.append("image", this.state.image);
    fd.append("genre", this.state.game);
    Axios.post("http://localhost:8888/forums/create", fd)
      .then(response => {
        this.setState({ postStatus: "yes" });
        console.log(response);
      })
      .catch(err => console.log(err));
  };
  render() {
    const { postStatus } = this.state;
    return (
      <form onSubmit={this.handleSubmit}>
        <div class="modal-body">
          <div class="form-group">
            <label for="exampleInputEmail1">Tiêu đề</label>
            <input
              onChange={this.handleChange}
              name="title"
              type="text"
              class="form-control"
              placeholder="Nhập nội dung"
            />
          </div>
          <div className="form-group">
            <label for="exampleFormControlFile1">Thể loại game</label>
            <select
              name="genre"
              class="form-control"
              onChange={this.handleChange}
            >
              <option value="">-- Thể loại game --</option>
              <option value="Liên Minh Huyền Thoại">
                Liên Minh Huyền Thoại
              </option>
              <option value="PUBG">PUBG</option>
              <option value="Liên Quân Mobile">Liên Quân Mobile</option>
              <option value="Dota 2">Dota 2</option>
              <option value="Khác">Khác</option>
            </select>
          </div>
          <div class="form-group">
            <label for="exampleFormControlFile1">Nội dung bài viết</label>
            <CKEditor
              name="content"
              editor={ClassicEditor}
              onInit={editor => {
                console.log("Editor is ready to use!", editor);
              }}
              onChange={(event, editor) => {
                const data = editor.getData();
                this.setState({ content: data });
              }}
            />
          </div>
          <div class="form-group">
            <label for="exampleFormControlFile1">Ảnh minh họa</label>
            <input
              name="image"
              type="file"
              onChange={this.handleVideoChange}
              class="form-control-file"
            />
          </div>
        </div>
        <button type="submit" className="btn btn-primary post-submit">
          Đăng bài viết
        </button>
        {postStatus === "yes" ? (
          <div class="status-message alert alert-success" role="alert">
            <i class="fas fa-check" /> Đã xong ! Hệ thống sẽ đăng trong vòng 5 -
            10 phút sau khi kiểm duyệt{" "}
          </div>
        ) : (
          [postStatus === "pending" ? <Pending /> : ""]
        )}
      </form>
    );
  }
}

export default uploadPost;
