import React, { Component } from "react";
import ForumItem from "./Forum.item.all";
import Loading from "../../Functions/Loading";
import Form from "./uploadPost";
import Axios from "axios";

class Forum extends Component {
  state = {
    total: null,
    data: []
  };
  componentDidMount() {
    Axios.get("http://localhost:8888/forums")
      .then(res => {
        this.setState({ total: res.data.counts, data: res.data.data });
        console.log(this.state.data);
      })
      .catch(err => console.log(err));
  }
  handleSubmit = e => {
    e.preventDefault();
    console.log("Done query");
  };
  render() {
    const { data } = this.state;
    return (
      <div className="container">
        <div className="title-section">
          <h1 class="cat-title"> Diễn đàn game thủ </h1>
          <h5 className="cat-subtitle">
            Lưu ý đọc kỹ nội quy ghim ở đầu trang trước khi đăng bài hoặc bình
            luận
          </h5>
        </div>
        <div className="row">
          <form className="col-sm-8" onSubmit={this.handleSubmit}>
            <input type="text" className="form-control" placeholder="Find..." />
            <i className="search-icon fas fa-search" />
          </form>
          <button
            data-toggle="modal"
            data-target="#createPost"
            className="col-sm-4 btn btn-success create-post-btn"
          >
            Tạo bài viết
          </button>
          <div
            class="modal fade"
            id="createPost"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div class="modal-dialog" role="document">
              <div class="modal-content">
                <div class="modal-header">
                  <h5 class="modal-title" id="exampleModalLabel">
                    Tạo bài viết
                  </h5>
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <Form />
              </div>
            </div>
          </div>
        </div>
        {data.length > 0 ? (
          data.map(item => {
            return <ForumItem data={item} key={item.news_id} />;
          })
        ) : (
          <Loading />
        )}
      </div>
    );
  }
}

export default Forum;
