import React from "react";
import { Link } from "react-router-dom";
const Highlight = ({ data }) => {
  return (
    <div className="highlight">
      <video width="300" controls>
        <source src={data.video} />
      </video>
      <div className="hl-details">
        <p className="hl-title">{data.title}</p>
        <p className="hl-description">
          {data.description} -{" "}
          {data.game === "Liên Minh Huyền Thoại" ? (
            <span class="badge badge-primary">{data.game}</span>
          ) : (
            [
              data.game === "PUBG" ? (
                <span class="badge badge-success">{data.game}</span>
              ) : (
                [
                  data.game === "Liên Quân Mobile" ? (
                    <span class="badge badge-waning">{data.game}</span>
                  ) : (
                    [
                      data.game === "Dota 2" ? (
                        <span className="badge badge-danger">{data.game}</span>
                      ) : (
                        <span className="badge badge-secondary">
                          {data.game}
                        </span>
                      )
                    ]
                  )
                ]
              )
            ]
          )}
        </p>
        <p className="hl-meta">
          Đăng bởi:{" "}
          <Link
            to={{
              pathname: `/users/${data.author_id}`
            }}
          >
            {" "}
            {data.author}
          </Link>{" "}
          - {data.created_at}
        </p>
      </div>
    </div>
  );
};

export default Highlight;
