import React from "react";
import { Link } from "react-router-dom";
const Newss = ({ data }) => {
  return (
    <div className="post-preview row">
      <div className="col-sm-3">
        <img src={data.image} className="post-image" alt="post" />
      </div>
      <div className="col-sm-9">
        <Link
          className="news-link"
          to={{
            pathname: `/news/${data.news_id}`,
            state: { id: data.news_id }
          }}
        >
          <h2 className="post-title">{data.title}</h2>
        </Link>
        <h5 className="post-subtitle">{data.subtitle}</h5>
        <p className="post-meta">
          Posted by{" "}
          <Link
            to={{
              pathname: `/users/${data.author_id}`
            }}
          >
            {" "}
            {data.author}
          </Link>{" "}
          on {data.created}
        </p>
      </div>
    </div>
  );
};

export default Newss;
