import React, { Component } from "react";
import axios from "axios";
import { Redirect, Link } from "react-router-dom";
class Register extends Component {
  state = {
    nickname: null,
    email: null,
    password: null,
    repassword: null,
    isVerify: null,
    isActive: false
  };
  handleChange = e => {
    const name = e.target.name;
    const value = e.target.value;
    this.setState({ [name]: value });
  };
  handleSubmit = e => {
    const state = this.state;
    e.preventDefault();
    if (state.password === state.repassword && state.isVerify === "on") {
      const data = {
        nickname: state.nickname,
        email: state.email,
        password: state.password
      };
      axios
        .post("http://localhost:8888/users/register", data)
        .then(response => {
          console.log(response.data);
          this.setState({ isActive: true });
        })
        .catch(error => {
          console.log(error.data);
          alert("Error occured!");
        });

      console.log(data);
    } else if (state.password !== state.repassword) {
      console.log("Password must match");
    } else if (state.isVerify === null) {
      console.log("You must accept the terms of use before register!");
    }
    console.log(state);
  };
  render() {
    const state = this.state;
    if (state.isActive) {
      return <Redirect to={{
        pathname: '/login',
        state:{
          email: state.email,
          password: state.password
        }
      }} />;
    }
    return (
      <form class="form-horizontal m-t-20" onSubmit={this.handleSubmit}>
        <div class="form-group row ">
          <div class="col-12 ">
            <input
              class="form-control form-control-lg"
              type="text"
              required=" "
              name="nickname"
              onChange={this.handleChange}
              placeholder="Nick name"
            />
          </div>
        </div>
        <div class="form-group row">
          <div class="col-12 ">
            <input
              class="form-control form-control-lg"
              type="text"
              required=" "
              name="email"
              onChange={this.handleChange}
              placeholder="Email"
            />
          </div>
        </div>
        <div class="form-group row">
          <div class="col-12 ">
            <input
              class="form-control form-control-lg"
              type="password"
              required=" "
              name="password"
              onChange={this.handleChange}
              placeholder="Mật khẩu"
            />
          </div>
        </div>
        <div class="form-group row">
          <div class="col-12 ">
            <input
              class="form-control form-control-lg"
              type="password"
              required=" "
              name="repassword"
              onChange={this.handleChange}
              placeholder="Nhập lại mật khẩu"
            />
          </div>
        </div>
        <div class="form-group row">
          <div class="col-md-12 ">
            <div class="custom-control custom-checkbox">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customCheck1"
                name="isVerify"
                onChange={this.handleChange}
              />
              <label class="custom-control-label" for="customCheck1">
                Tôi đồng ý với <a href=".">thỏa thuận về sử dụng </a>
              </label>
            </div>
          </div>
        </div>
        <div class="form-group text-center ">
          <div class="col-xs-12 p-b-20 ">
            <button class="btn btn-block btn-lg btn-info " type="submit ">
              ĐĂNG KÝ
            </button>
          </div>
        </div>
        <div class="form-group m-b-0 m-t-10 ">
          <div class="col-sm-12 text-center ">
            Đã có tài khoản?{"  "}
            <b>
              <Link to="/login">Đăng nhập</Link>{" "}
            </b>
          </div>
        </div>
      </form>
    );
  }
}

export default Register;
