import React from "react";
import Posts from "../components/News/News";
import News from "../components/Forum/Posts";
import Highlights from "../components/Highlight/Highlights";
// import SideNav from "../components/ui/sidenav";
const Home = () => {
  return (
    <div className="container">
      <div className="row">
        <Posts />
        <News styleName="col-sm-4" />
      </div>
      <div>
        <Highlights />
      </div>
    </div>
  );
};

export default Home;
