import React, { Component } from "react";
import "../../App.css";
import avatar from "../../images/avatar_default.png";

class Sidenav extends Component {
  render() {
    return (
      <div id="mySidenav" class="sidenav">
        <div className="dropdown">
          <div id="drop" className="route-nav-item dropbtn">
            <img
              src={avatar}
              className="icon-route"
              alt="Home Route"
              style={{ width: 50, borderRadius: 50 }}
            />
            <div class="dropdown-content">
              <a className="dp-item" href=".">
                Trang cá nhân
              </a>
              <a className="dp-item" href=".">
                Cài đặt thông tin
              </a>
              <a className="dp-item-bottom" href=".">
                Đăng xuất
              </a>
            </div>
          </div>
        </div>
        <div className="dropdown">
          <div id="drop" className="route-nav-item dropbtn">
            <i className="fas fa-scroll icon-route" style={{ fontSize: 22 }} />
          </div>
        </div>
        <div className="dropdown">
          <div id="drop" className="route-nav-item dropbtn">
            <i
              className="fas fa-comment-dots icon-route"
              style={{ fontSize: 22 }}
            />
          </div>
        </div>
        <div className="dropdown">
          <div id="drop" className="route-nav-item dropbtn">
            <i class="fas fa-users icon-route" style={{ fontSize: 22 }} />
            <div class="dropdown-content">
              <a className="dp-item" href=".">
                Club messages
              </a>
              <a className="dp-item-bottom" href=".">
                Trang Câu lạc bộ
              </a>
            </div>
          </div>
        </div>
        <div className="dropdown">
          <div id="drop" className="route-nav-item dropbtn">
            <i class="fas fa-trophy icon-route" style={{ fontSize: 22 }} />
            <div class="dropdown-content">
              <a className="dp-item" href=".">
                Danh sách giải đấu
              </a>
              <a className="dp-item" href=".">
                Đội eSports{" "}
              </a>
              <a className="dp-item-bottom" href=".">
                Giải đấu của tôi
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Sidenav;
