import React from "react";
import { Link } from "react-router-dom";
import avatar from "../../images/avatar_default.png";
const Navbar = ({ isAuth, accountName, removeStorage }) => {
  if (isAuth === false) {
    // window.location.reload();
  }
  return (
    <nav
      className="navbar navbar-expand-lg navbar-light fixed-top"
      id="mainNav"
    >
      <div className="container">
        <Link to="/" className="navbar-brand" href=".">
          VN eSports
        </Link>
        <button
          className="navbar-toggler navbar-toggler-right"
          type="button"
          data-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          Menu
          <i className="fas fa-bars" />
        </button>
        <div className="collapse navbar-collapse" id="navbarResponsive">
          <ul className="navbar-nav ml-auto">
            <li className="nav-item">
              <Link to="/" className="nav-link">
                Trang chủ
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/news/all">
                Tin tức
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/forum/all" className="nav-link">
                Diễn đàn
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/highlights/all">
                Highlights
              </Link>
            </li>
            {isAuth === true ? (
              <li className="nav-item">
                <a
                  className="nav-link"
                  style={{ fontWeight: 600, padding: 8, fontSize: 15 }}
                  href="."
                >
                  <img
                    src={avatar}
                    style={{ width: 30, borderRadius: 50 }}
                    alt="avatar"
                  />{" "}
                  {accountName}
                </a>
              </li>
            ) : (
              <ul className="navbar-nav ml-auto">
                {" "}
                <li className="nav-item auth-btn">
                  <Link className="nav-link btn-danger nav-auth" to="/login">
                    Đăng nhập
                  </Link>
                </li>
                <li className="nav-item auth-btn">
                  <Link
                    className="nav-link btn-success nav-auth"
                    to="/register"
                  >
                    Đăng ký
                  </Link>
                </li>
              </ul>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
