import React from "react";
import frontImage from "../../images/home2.jpg";
import Navbar from "./Navbar";
import logo from "../../images/logo-brand.png";
import {
  initializeStore,
  getViewData,
  storeViewData
} from "../../Functions/LocalStorage";
import Axios from "axios";
class Header extends React.Component {
  state = {
    id: ""
  };
  authData = this.props.authData;
  componentDidMount() {
    console.log(this.authData);
    // If session was closed, the authData will be saved in the storage. Next time when user access, the data will be retrieved from the storage. 
    if (this.authData === undefined) {
      let id = getViewData("id");
      Axios.get(`http://localhost:8888/users/${id}`)
        .then(response => {
          console.log(response.data);
        })
        .catch(err => console.log(err));
    } else {
      storeViewData("id", this.props.authData);
      Axios.get(`http://localhost:8888/users/${this.authData}`)
        .then(response => {
          console.log(response.data);
        })
        .catch(err => console.log(err));
    }
  }

  removeStorage = () => {
    initializeStore();
    window.location.reload();
  };
  render() {
    return (
      <div className="header">
        <Navbar
          isAuth={true}
          accountName={"Admin"}
          removeStorage={this.removeStorage}
        />
        <header
          className="masthead"
          style={{ backgroundImage: `url(${frontImage})` }}
        >
          <div className="overlay" />
          <div className="container">
            <div className="row">
              <div className="col-lg-8 col-md-10 mx-auto header-content">
                <div className="site-heading">
                  <img alt="brand" src={logo} class="brand-header" />
                  <span className="subheading">
                    Mạng lưới eSports dành cho game thủ
                  </span>
                  <form class="header-search">
                    <input
                      className="form-control search-bar"
                      type="text"
                      placeholder="Tìm kiếm gì đó..."
                    />
                  </form>
                </div>
              </div>
            </div>
          </div>
        </header>
      </div>
    );
  }
}

export default Header;
