import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { Redirect } from "react-router-dom";
import { storeViewData } from "../../Functions/LocalStorage";
class Login extends Component {
  state = {
    email: null,
    password: null,
    active: false,
    id: null
  };
  handleChange = e => {
    const name = e.target.name;
    this.setState({ [name]: e.target.value });
  };
  handleSubmit = e => {
    e.preventDefault();
    const user = {
      email: this.state.email,
      password: this.state.password
    };
    axios
      .post("http://localhost:8888/users/login", user)
      .then(response => {
        this.setState({ active: true, id: response.data.id });
        storeViewData("auth-token", response.data.auth_token);
      })
      .catch(error => console.log(error));
  };
  render() {
    const state = this.state;
    if (state.active) {
      return (
        <Redirect
          to={{
            pathname: "/",
            state: this.state.id
          }}
        />
      );
    }
    return (
      <form onSubmit={this.handleSubmit}>
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text" id="basic-addon1">
              <i class="far fa-user" />
            </span>
          </div>
          <input
            type="email"
            name="email"
            class="form-control form-control-lg"
            placeholder="Email"
            aria-label="Email"
            aria-describedby="basic-addon1"
            onChange={this.handleChange}
          />
        </div>
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text" id="basic-addon2">
              <i class="fas fa-lock" />
            </span>
          </div>
          <input
            type="password"
            name="password"
            class="form-control form-control-lg"
            placeholder="Mật khẩu"
            aria-label="Mật khẩu"
            aria-describedby="basic-addon1"
            onChange={this.handleChange}
          />
        </div>
        <div class="form-group row">
          <div class="col-md-12">
            <div class="custom-control custom-checkbox">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customCheck1"
              />
              <label class="custom-control-label" htmlFor="customCheck1">
                Nhớ mật khẩu
              </label>
              <a href="." id="to-recover" class="text-dark float-right">
                <i class="fa fa-lock m-r-5" /> Quên mật khẩu ?
              </a>
            </div>
          </div>
        </div>
        <div class="form-group text-center">
          <div class="col-xs-12 p-b-20">
            <button class="btn btn-block btn-lg btn-info" type="submit">
              Đăng nhập
            </button>
          </div>
        </div>
        <div class="form-group m-b-0 m-t-10">
          <div class="col-sm-12 text-center">
            Không có tài khoản?{" "}
            <b>
              <Link to="/register">Đăng ký ngay</Link>
            </b>
          </div>
        </div>
      </form>
    );
  }
}

export default Login;
