import React, { Component } from "react";
import Highlight from "./Highlight.item";
import axios from "axios";
import Loading from "../../Functions/Loading";
class Highlights extends Component {
  state = {
    highlights: []
  };
  componentDidMount() {
    axios
      .get("http://localhost:8888/highlights")
      .then(res => this.setState({ highlights: res.data.data }))
      .catch(err => console.log(err));
  }

  render() {
    const highlight = this.state.highlights;
    return (
      <div className="highlight-section">
        <h5>Video highlight nổi bật</h5>
        <div className="col-sm-12 row ">
          {highlight.length > 0 ? (
            this.state.highlights.map(highlight => {
              return <Highlight data={highlight} key={highlight._id} />;
            })
          ) : (
            <Loading />
          )}
        </div>
      </div>
    );
  }
}

export default Highlights;
