import React from "react";
import { Link } from "react-router-dom";
const News = ({ data }) => {
  return (
    <div className="post-preview row">
      <div className="col-sm-2">
        <img src={data.image} className="post-image" alt="post" />
      </div>
      <div className="col-sm-10">
        <Link
          className="news-link"
          to={{
            pathname: `/news/${data.news_id}`,
            state: { id: data.news_id }
          }}
        >
          <h2 className="post-title">{data.title}</h2>
        </Link>
        <p className="post-meta">
          Posted by{" "}
          <Link
            to={{
              pathname: `/users/${data.author_id}`
            }}
          >
            {" "}
            {data.author}
          </Link>{" "}
          on {data.created}
        </p>
        <p className="post-interaction">
          {data.interaction.likes} <i class="fas fa-thumbs-up" /> -{"   "}
          {data.interaction.shares} <i class="fas fa-share-square" /> - {"  "}
          {data.comments.comment_counts} comments
        </p>
      </div>
    </div>
  );
};

export default News;
