import React from "react";
import { Link } from "react-router-dom";
const Highlight = ({ data }) => {
  return (
    <div className="highlight">
      <video width="300" controls>
        <source src={data.video} />
      </video>
      <div className="hl-details">
        <p className="hl-title">{data.title}</p>
        <p className="hl-description">{data.description}</p>
        <p className="hl-meta">
          Đăng bởi:{" "}
          <Link
            to={{
              pathname: `/users/${data.author_id}`
            }}
          >
            {" "}
            {data.author}
          </Link>
        </p>
        <p className="hl-meta">Ngày đăng: {data.created}</p>
        <p className="post-interaction">
          {data.interaction.likes} <i class="fas fa-thumbs-up" /> -{"   "}
          {data.comments.comment_counts} bình luận
        </p>
      </div>
    </div>
  );
};

export default Highlight;
