import React, { Component } from "react";
import { Link } from "react-router-dom";
import Axios from "axios";
import moment from "moment";
import { Markup } from "interweave";

export default class Detail extends Component {
  state = {
    data: [],
    comments: [],
    comment: null
  };
  componentDidMount() {
    let first_id = this.props.location.state.id;
    console.log(first_id);
    Axios.get(`http://localhost:8888/forums/${first_id}`)
      .then(res => {
        this.setState({
          data: res.data.data,
          comments: res.data.data.comments
        });
        console.log(this.state);
      })
      .catch(err => console.log(err));
  }
  handleChange = e => {
    this.setState({ comment: e.target.value });
  };
  handleSubmit = e => {
    e.preventDefault();
    let second_id = this.props.location.state.id;
    let post = {
      content: this.state.comment,
      token: window.localStorage.getItem("auth-token")
    };
    Axios.post(`http://localhost:8888/forums/${second_id}/comment`, post);
  };
  render() {
    const { data } = this.state;
    const { comments } = this.state;
    const comment = comments.map(item => (
      <div
        className="cmt-box"
        style={{
          padding: 20,
          border: "1px solid gainsboro",
          width: "50%",
          marginBottom: 12
        }}
      >
        <div className="cmt-author row">
          <img
            alt={item.author.nickname}
            className="avatar-icon"
            src={item.author.avatarUrl}
          />
          <h5>{item.author.nickname}</h5>
        </div>
        <div className="cmt-content row">{item.content}</div>
        <div className="cmt-meta row">
          <div className="cmt-created">
            <i>{moment(item.created).format("LLL")}</i>
          </div>{" "}
          <div className="cmt-likes" style={{ color: "green" }}>
            {item.likes} likes
          </div>
        </div>
      </div>
    ));
    // console.log(Object.values(data.comments));
    return (
      <div className="container">
        <div className="summary row">
          <img
            src={data.image}
            alt={data.title}
            className="col-sm-3 post-main-img"
          />
          <div className="col-sm-8 summary-detail">
            <h3 className="summary-detail-title">{data.title}</h3>
            <h5 className="summary-detail-author">
              Người đăng:{" "}
              <Link
                to={{
                  pathname: `/users/${data.author_id}`,
                  state: { userId: data.author_id }
                }}
              >
                {data.author_name}
              </Link>
            </h5>
            <h6 className="summary-detail-author">
              Game: Liên Minh Huyền Thoại
            </h6>
            <i className="summary-detail-time">Đăng vào {data.created}</i>
            <div class="interaction-button-group">
              <button className="btn btn-primary btn-interaction">
                {data.likes} Likes
              </button>
              <button className="btn btn-outline-danger btn-interaction">
                {data.shares} Shares
              </button>
              <a
                href="#comments"
                className="btn btn-outline-secondary btn-interaction"
              >
                {data.comment_counts} bình luận
              </a>
            </div>
          </div>
        </div>
        <hr />
        <div class="post-main-content">
          {" "}
          <Markup content={data.content} />{" "}
        </div>
        <hr />
        <div className="post-main-comments" id="comments">
          <h4 className="">Bình luận</h4>
          <form onSubmit={this.handleSubmit} className="form-group row">
            <input
              required
              type="text"
              name="comment"
              onChange={this.handleChange}
              className="form-control col-sm-8"
              placeholder="Viết bình luận..."
            />
            <button type="submit" className="btn btn-primary">
              Gửi
            </button>
          </form>
          <div className="comments-list">{comment}</div>
        </div>
      </div>
    );
  }
}
