import React from "react";
import "./clean-blog.css";
import "./App.css";
import Header from "./components/Header/Header";
import Home from "./components/Home";
import Forum from "./components/Forum/Forum.all";
import News from "./components/News/News.all";
import Highlight from "./components/Highlight/Highlight.all";
import Profile from "./components/User/Profile";
import SideNav from "./components/ui/sidenav";
import NewsDetail from "./components/News/News.detail";
import PostDetail from "./components/Forum/Forum.detail";

import { BrowserRouter, Switch, Route } from "react-router-dom";
class App extends React.Component {
  render() {
    const authData = this.props.location.state;
    return (
      <BrowserRouter>
        <div className="main">
          <Header authData={authData} />
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/forum/all" component={Forum} />
            <Route path="/news/all" component={News} />
            <Route path="/highlights/all" component={Highlight} />
            <Route path="/users/:userId" component={Profile} />
            <Route path="/forum/:forumId" component={PostDetail} />
            <Route path="/news/:newsId" component={NewsDetail} />
          </Switch>
          <SideNav />
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
